import React, { createContext, useEffect, useState } from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { getdata } from "../feature/cartSlice";
export const ApiData = createContext();
const Data = ({ children }) => {
  const [maindata, setMaindata] = useState([]);
  const [catdata, setCatdata] = useState([]);
  const dispatch = useDispatch();
  useEffect(() => {
    axios.get("https://fakestoreapi.com/products").then((res) => {
      setMaindata(res.data);
      // console.log(res.data);

      // console.log(maindata.prototype.quantity = function(){
      //   return 0
      // })
      // console.log(maindata)
    });
  }, []);
  useEffect(() => {
    axios.get("https://fakestoreapi.com/products/categories").then((res) => {
      setCatdata(res.data);
    });
  });
  useEffect(() => {
    dispatch(getdata(maindata));
    // console.log(maindata)
  });

  return (
    <>
      <ApiData.Provider value={{ maindata, setMaindata, catdata, setCatdata }}>
        {children}
      </ApiData.Provider>
    </>
  );
};
export default Data;
