import React, { useContext, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./navbar.css";
import IconButton from "@mui/material/IconButton";
import { Typography } from "@mui/material";
import PersonOutlineOutlinedIcon from "@mui/icons-material/PersonOutlineOutlined";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import { ApiData } from "./Data";
import { useDispatch, useSelector } from "react-redux";
import {
  getCartTotal,
  getfavitems,
  getfavitemstotal,
} from "../feature/cartSlice";
const Navbar = () => {
  const navigate = useNavigate();
  const [categoriesdata, setCategoriesData] = useState([]);
  const catdata = useContext(ApiData);
  const { cart, totalQuantity, favitems, totalfavitems } = useSelector(
    (state) => state.allcart
  );
  const dispatch = useDispatch();
  useEffect(() => {
    setCategoriesData(catdata.catdata);
  });
  useEffect(() => {
    dispatch(getCartTotal());
  }, [cart]);

  useEffect(() => {
    dispatch(getfavitemstotal());
  }, [favitems]);
  // console.log(categoriesdata);
  const cartshow = () => {
    navigate("/cart");
  };
  const categorypage = (catName) => {
    navigate(`/singlecat/${catName}`);
  };
  const favoriteshow = () => {
    navigate("/favpage");
  };
  return (
    <>
      <section className="bg-white ">
        <div className="bg-white">
          <div className="container">
            <nav className="navbar navbar-expand-lg bg-white pb-4">
              {/* <div className="container"> */}
              {/* <div className="d-flex"> */}
              <a className="navbar-brand" href="#">
                <img src="images/logo-dark.png" alt="" />
              </a>
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavDropdown"
                aria-controls="navbarNavDropdown"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div
                className="collapse navbar-collapse justify-content-center"
                id="navbarNavDropdown"
              >
                <ul className="navbar-nav">
                  <li className="nav-item">
                    <Link
                      className="nav-link active"
                      aria-current="page"
                      to="/"
                    >
                      Home
                    </Link>
                  </li>

                  {/* <li className="nav-item">
                  <Link className="nav-link" to="/singleproduct">
                    Singleproduct
                  </Link>
                </li> */}

                  <li className="nav-item dropdown">
                    <a
                      className="nav-link dropdown-toggle"
                      href="#"
                      id="navbarDropdownMenuLink"
                      role="button"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                    >
                      Categories
                    </a>
                    <ul
                      className="dropdown-menu"
                      aria-labelledby="navbarDropdownMenuLink"
                    >
                      {categoriesdata.map((items, index) => {
                        return (
                          <li
                            key={index}
                            className="dropdown-item"
                            to="#"
                            onClick={() => {
                              categorypage(items);
                            }}
                          >
                            {items}
                          </li>
                        );
                      })}
                    </ul>
                  </li>
                </ul>
              </div>
              {/* </div> */}
              {/* </div> */}
              <div className=" col-sm-2 menu-icons d-flex justify-content-around">
                {/* <IconButton>
                  <PersonOutlineOutlinedIcon></PersonOutlineOutlinedIcon>
                </IconButton> */}

                <div className="cart-icon position-relative">
                  <IconButton
                    className="clr-primary "
                    color="primary"
                    onClick={favoriteshow}
                  >
                    <FavoriteBorderOutlinedIcon />
                    <span
                      style={{
                        backgroundColor: "#333333",
                        color: "#fff",
                        left: "20px",
                        fontSize: "1rem",
                      }}
                      className="rounded-circle px-2 position-absolute top-0"
                    >
                      {totalfavitems}
                    </span>
                  </IconButton>
                </div>
                <div className="cart-icon d-inline-flex align-items-center position-relative">
                  <IconButton className="clr-primary " color="primary">
                    <ShoppingCartOutlinedIcon onClick={cartshow} />
                    <span
                      style={{
                        backgroundColor: "#333333",
                        color: "#fff",
                        left: "20px",
                        fontSize: "1rem",
                      }}
                      className="rounded-circle px-2 position-absolute top-0"
                    >
                      {totalQuantity}
                    </span>
                  </IconButton>
                  <Typography variant="h6" className="ps-2">
                    {" "}
                    $00.00
                  </Typography>
                </div>
              </div>
            </nav>
          </div>
        </div>
      </section>
    </>
  );
};
export default Navbar;
