import { Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import IconButton from "@mui/material/IconButton";
import EmailIcon from '@mui/icons-material/Email';
import { ApiData } from "./Data";
const Footer = () => {
  const getcat =  useContext(ApiData)
  const [categorydata , setCategorydata] = useState([])
  useEffect(()=>{
    setCategorydata(getcat.catdata)
  })
  return (
    <>
      <section id="footer">
        <div className="container-fluid mt-4">
          <div className="container">
            <div className="row clr-secondary">
              <div className="col-sm-4 pe-5 ">
                <div className="mb-3">
                  <img src="images/logo-light.png" alt=""  style={{width: "200px"}}className="img-fluid"/>
                </div>
                <Typography variant="body2" className="lh-2 ">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Eos
                  totam voluptatibus rerum quos, incidunt voluptatum asperiores
                  vitae praesentium a qui dolores placeat? Aliquid, iusto
                  aliquam distinctio commodi magnam laudantium magni.
                </Typography>
                <div className="social-icons  py-3">
                  <IconButton className="clr-secondary px-0">
                    <FacebookIcon/>
                  </IconButton>
                  <IconButton className="clr-secondary">
                    <TwitterIcon/>
                  </IconButton>
                  <IconButton className="clr-secondary px-0">
                    <InstagramIcon/>
                  </IconButton>
                  <IconButton className="clr-secondary">
                    <LinkedInIcon/>
                  </IconButton>
                </div>
              </div>
              <div className="col-sm-2">
                <Typography variant="h5" className="fw-bold mb-3">
                  Quick Links
                </Typography>
                <ul>
                  <li className="pb-2">
                    <Link to="#">Terms and condition</Link>
                  </li>
                  <li className="pb-2">
                    <Link to="#">Privacy Policy</Link>
                  </li>
                  <li className="pb-2">
                    <Link to="#">Help</Link>
                  </li>
                </ul>
              </div>
              <div className="col-sm-3">
                <Typography variant="h5" className="fw-bold mb-3">
                  Categories
                </Typography>

                <ul>
                  {categorydata.map((items,index)=>{

                 return( <li className="pb-2">
                    <Link to="#">{items}</Link>
                  </li>)
                  })}
                 
                </ul>
              </div>
              <div className="col-sm-3">
                <Typography variant="h5" className="fw-bold mb-3">
                  Contact Us
                </Typography>
                <ul>
                  <li className="pb-3 d-flex justify-content-around align-items-top gap-2" >
                   <HomeIcon/> <span>48764 Howard Forge Apt. 421\nVanessaside, VT 79393</span>
                  </li>
                  <li className="pb-3 d-flex  align-items-top gap-2" >
                   <LocalPhoneIcon/> <span>+467 876 356 876</span>
                  </li>
                  <li className="pb-3 d-flex align-items-top gap-2" >
                   <EmailIcon/> <span>Metro@gmail.com</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <hr className="clr-secondary"/>
          <div className="copyright clr-secondary text-center">
          <Typography variant="body2"className="pb-2">© Copyright 2014, Metro Woocommerce.</Typography>
          </div>
        </div>
      </section>
    </>
  );
};

export default Footer;
