import React from "react";
import { useSelector } from "react-redux/es/hooks/useSelector";
import Globalbanner from "./Globalbanner";
import { Typography } from "@mui/material";
import IconButton from "@mui/material/IconButton";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import Button from "@mui/material/Button";
import ShuffleIcon from "@mui/icons-material/Shuffle";
import { ToastContainer, toast } from "react-toastify";
import VisibilityIcon from "@mui/icons-material/Visibility";
import { Rating } from "react-simple-star-rating";
import { useDispatch } from "react-redux";
import { getfavitems } from "../feature/cartSlice";
import { useNavigate } from "react-router-dom";

const Favorite = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { favitems } = useSelector((state) => state.allcart);
  console.log(favitems);

  const favoriteitems = (favproduct) => {
    dispatch(getfavitems(favproduct));
    const favdata = favitems.filter((items, index) => {
      return favproduct.id === items.id;
    });
    if (favdata.length > 0) {
      toast("Product Removed From  Favorite");
    } else {
      toast("Product Added To Favorite");
    }
  };

  const singleprop = (data) => {
    navigate(`/singleproduct/${data}`);
  };

  return (
    <>
      <ToastContainer />
      <Globalbanner title="Favorite" data = "" />
      <div className="container">
        <div className="row">
          {favitems.map((items, index) => {
            return (
              <div
                className="col-sm-12 col-lg-3 col-md-3 mb-3 product"
                key={index}
              >
                <div className="card">
                  <div className="p-5 ">
                    <img
                      src={items.image}
                      class=" card-img card-img-top "
                      alt="..."
                    />
                  </div>
                  <div class="card-body py-1">
                    <h5 class="card-title m-0">{items.title.slice(0, 20)}</h5>
                    <Typography
                      variant="body2"
                      className="text-muted"
                      gutterBottom
                    >
                      {items.category}
                    </Typography>
                    <Rating
                      initialValue={items.rating.rate}
                      readonly
                      size={24}
                      className="mb-2"
                    />
                    <div className="d-flex justify-content-between align-items-center py-2">
                      <h4>${items.price}</h4>
                      <p className="clr-green">In Stock</p>
                    </div>
                  </div>
                  <div className="position-relative showparent">
                    <div className="position-absolute showdetail px-2 pt-2">
                      <div className="d-flex justify-content-between gap-2 ">
                        <IconButton
                          className="p-2"
                          onClick={() => favoriteitems(items)}
                        >
                          <FavoriteBorderOutlinedIcon />
                        </IconButton>
                        <Button
                          onClick={() => {
                            singleprop(items.id);
                          }}
                          variant="contained"
                          className="flex-fill rounded-0 btn-card"
                          sx={{ background: "#333" }}
                        >
                          Show Details
                        </Button>
                        <IconButton className="p-2">
                          <VisibilityIcon />
                        </IconButton>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Favorite;
