import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import SearchIcon from "@mui/icons-material/Search";
import VisibilityIcon from "@mui/icons-material/Visibility";
import Button from "@mui/material/Button";
import Globalbanner from "./Globalbanner";
import { ApiData } from "./Data";
import { Typography } from "@mui/material";
import { Rating } from "react-simple-star-rating";
const Singlecategory = () => {
  const [catData, setCatdata] = useState([]);
  const [mydata, setMyData] = useState([])
  const [toprated, setToprated] = useState([])
  const singlename = useParams();
  const navigate = useNavigate();
  const getdata = useContext(ApiData)
  useEffect(() => {
    axios
      .get(`https://fakestoreapi.com/products/category/${singlename.cname}`)
      .then((res) => {
        setCatdata(res.data);
      });
  });

  const singleprop = (id) => {
    navigate(`/singleproduct/${id}`);
  };

  useEffect(()=>{
    setMyData(getdata.maindata);
    const topproduct = mydata.filter((items, index) => {
      if (items.rating.rate > 4.6) {
        return items;
      }
    });
    setToprated(topproduct);
  })
  return (
    <>
      <section>
        <Globalbanner data={singlename.cname} title={singlename.cname} />
      </section>
      <section id="catdata" className="mt-5">
        <div className="container">
          <div className="row">
            {catData.map((items, index) => {
              return (
                <div className="col-sm-3 mb-3 product" key={index}>
                  <div className="card">
                    <div className="p-5 ">
                      <img
                        src={items.image}
                        class=" card-img card-img-top "
                        alt="..."
                      />
                    </div>
                    <div class="card-body py-1">
                      <h5 class="card-title m-0">{items.title.slice(0, 20)}</h5>
                      <Typography
                        variant="body2"
                        className="text-muted"
                        gutterBottom
                      >
                        {items.category}
                      </Typography>
                      <Rating
                        initialValue={items.rating.rate}
                        readonly
                        size={24}
                        className="mb-2"
                      />
                      <div className="d-flex justify-content-between align-items-center py-2">
                        <h4>${items.price}</h4>
                        <p className="clr-green">In Stock</p>
                      </div>
                    </div>
                    <div className="position-relative showparent">
                      <div className="position-absolute showdetail px-2 pt-2">
                        <div className="d-flex justify-content-between gap-2 ">
                          <IconButton className="p-2">
                            <FavoriteBorderOutlinedIcon />
                          </IconButton>
                          <Button
                            onClick={() => {
                              singleprop(items.id);
                            }}
                            variant="contained"
                            className="flex-fill rounded-0 btn-card"
                            sx={{ background: "#333" }}
                          >
                            Show Details
                          </Button>
                          <IconButton className="p-2">
                            <VisibilityIcon />
                          </IconButton>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>

      {/* top rated product */}
      <section className="mt-5">
        <div className="container">
          <h3 className="text-center pt-5 pb-5">Our Top Rated Products</h3>
          <div className="row g-5">
            {toprated.map((items, index) => {
              return (
                <>
                  <div className="col-sm-1"></div>
                  <div className="col-sm-5 position-relative">
                    <div className="card toprated-card">
                      <div className="p-5 text-center">
                        <img
                          src={items.image}
                          class="ratedimg img-fluid sizing"
                          // style={{ width: "300px", height: " 300px" }}
                          alt="..."
                        />
                      </div>
                    </div>
                    <div className="card-body card-top-reated-pos">
                      <h3>{items.title.slice(0, 13)}</h3>
                      <p className="text-muted">
                        {items.description.slice(0, 20)}
                        <br />
                        {items.description.slice(20, 40)}
                      </p>
                      <div>
                        <Rating
                          initialValue={items.rating.rate}
                          readonly
                          size={20}
                        />
                      </div>
                      <button
                        className="btn slider-left-btn1"
                        onClick={() => {
                          singleprop(items.id);
                        }}
                      >
                        Shop Now
                      </button>
                    </div>
                  </div>
                </>
              );
            })}
          </div>
        </div>
      </section>
    </>
  );
};
export default Singlecategory;
