import React from "react";
import Breadcrumb from 'react-bootstrap/Breadcrumb';

const Globalbanner = (props) => {
  
  return (
    <>
      <section id="globalbanner">
        <div className="container pt-7 pb-7 text-center">
          <h1>{props.title}</h1>
          <Breadcrumb>
            <Breadcrumb.Item href="/">Home</Breadcrumb.Item>
            <Breadcrumb.Item href="" >
             Products
            </Breadcrumb.Item>
            <Breadcrumb.Item >{props.data.category}</Breadcrumb.Item>
            <Breadcrumb.Item active>{props.data.title}</Breadcrumb.Item>
          </Breadcrumb>
        </div>
      </section>
    </>
  );
};

export default Globalbanner;
