import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import {
  getCartTotal,
  removeItem,
  increaseQuantity,
  decreaseQuantity,
} from "../feature/cartSlice";
import Globalbanner from "./Globalbanner";
export default function Cart() {
  // const [cartProduct, setCartProduct] = useState([]);
  // const [singleAmount, setSingleAmount] = useState([]);
  // const singleproducttotal = (event, price) => {
  //   let quantity = event.target.value;
  //   let pp = price;
  //   setSingleAmount(quantity * pp);
  //   // console.log(
  //   //   quantity*pp
  //   // )
  // };
  const { cart, totalQuantity, totalPrice } = useSelector(
    (state) => state.allcart
  );
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getCartTotal());
  }, [cart]);
  return (
    <>
      <section>
        <Globalbanner title="Cart" data="" />
      </section>
      <div>
        <section className="h-100 gradient-custom">
          <div className="container py-5">
            <div className="row d-flex justify-content-center my-4">
              <div className="col-md-8">
                <div className="card mb-4">
                  <div className="card-header py-3">
                    <h5 className="mb-0 clr-secondary">
                      Cart - {cart.length} items
                    </h5>
                  </div>
                  <div className="card-body">
                    {cart.map((data, index) => {
                      return (
                        <div className="row" key={index}>
                          <div className="col-lg-3 col-md-12 mb-4 mb-lg-0">
                            <div
                              className="bg-image hover-overlay hover-zoom ripple rounded"
                              data-mdb-ripple-color="light"
                            >
                              <img src={data.image} className="w-100" alt="" />
                            </div>
                          </div>

                          <div className="col-lg-5 col-md-6 mb-4 mb-lg-0 pt-2">
                            <h5>
                              <b>{data.title}</b>
                            </h5>
                            <p className="text-muted">
                              {data.description.slice(0, 59)}
                            </p>

                            <button
                              type="button"
                              className="btn btn-sm me-1 mb-2 btn-cart"
                              data-mdb-toggle="tooltip"
                              title="Remove item"
                              onClick={() => dispatch(removeItem(data.id))}
                            >
                              <DeleteIcon />
                            </button>
                          </div>

                          <div className="col-lg-4 col-md-6 mb-4 mb-lg-0 pt-2">
                            <div
                              className="d-flex mb-4"
                              style={{ maxWidth: "300px" }}
                            >
                              <button
                                className="btn btn-cart p-3 me-2"
                                onClick={() => {
                                  dispatch(increaseQuantity(data.id));
                                }}
                              >
                                <AddIcon />
                              </button>

                              <div className="">
                                <label className="form-label" for="form1">
                                  Quantity
                                </label>
                                <input
                                  id="form1"
                                  min="0"
                                  name="quantity"
                                  type="number"
                                  value={data.quantity}
                                  className="form-control"
                                />
                              </div>

                              <button
                                className="btn btn-cart p-3 ms-2"
                                onClick={() => {
                                  dispatch(decreaseQuantity(data.id));
                                }}
                              >
                                <RemoveIcon />
                              </button>
                            </div>

                            <h3 className="text-start text-md-center pt-3">
                              <strong>${data.price}</strong>
                            </h3>
                          </div>
                          <hr className="my-4" />
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
              <div className="col-md-4">
                <div className="card mb-4">
                  <div className="card-header py-3">
                    <h5 className="mb-0 clr-secondary">Summary</h5>
                  </div>
                  <div className="card-body">
                    <ul className="list-group list-group-flush">
                      <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 pb-0">
                        <h6>Total Quantity</h6>
                        <span>{totalQuantity}</span>
                      </li>

                      <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 mb-3">
                        <div>
                          <h4>
                            <strong>Total amount</strong>
                          </h4>
                        </div>
                        <h5>
                          <strong>${totalPrice}</strong>
                        </h5>
                      </li>
                    </ul>

                    <button
                      type="button"
                      className="btn checkout-btn  btn-lg btn-block"
                    >
                      Go to checkout
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
