import { Typography } from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import { addToCart } from "../feature/cartSlice";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Globalbanner from "./Globalbanner";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import VisibilityIcon from "@mui/icons-material/Visibility";
import { Rating } from "react-simple-star-rating";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import {
  getCartTotal,
  removeItem,
  increaseQuantity,
  decreaseQuantity,
} from "../feature/cartSlice";

const Singleproduct = () => {
  const [productData, setProductData] = useState([]);
  const [realtedCatData, setRealatedCatData] = useState([]);
  const id = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { cart, totalQuantity, totalPrice } = useSelector(
    (state) => state.allcart
  );
  // console.log(cart);
  useEffect(() => {
    axios
      .get(`https://fakestoreapi.com/products/${id.productId}`)
      .then((res) => {
        setProductData(res.data);
      });
  });

  useEffect(() => {
    axios
      .get(`https://fakestoreapi.com/products/category/${productData.category}`)
      .then((res) => {
        setRealatedCatData(res.data);
      });
  });

  const singleprop = (dataId) => {
    navigate(`/singleproduct/${dataId}`);
  };

  const addedcart = () => {
    dispatch(addToCart(productData));
    const data = cart.filter((items, index) => {
      return productData.id === items.id;
    });
    // console.log(data)
    if (data.length > 0) {
      console.log("chutiya");
      toast("Product Already Added to Cart");
    } else {
      toast("Product Added to Cart");
    }
  };

  return (
    <>
      <ToastContainer />
      <Globalbanner data={productData} title="Shops" />
      <section className="mt-7">
        <div className="container">
          <div className="">
            <div className="row ">
              <div className="col-sm-5 toprated-card p-5 text-center">
                <img
                  src={productData.image}
                  alt=""
                  className="img-fluid ratedimg"
                  style={{ width: "300px" }}
                />
              </div>
              <div className="col-sm-7 px-5">
                <h2 className="mb-3">{productData.title}</h2>
                <h3 className="mb-3">${productData.price}</h3>
                <p className="mb-1">
                  <span className="text-muted">Availability :</span>
                  <span> In Stock</span>
                </p>
                <p className="mb-1">
                  <span className="text-muted">Category :</span>
                  <span> {productData.category}</span>
                </p>
                <p className="mb-4">
                  <span className="text-muted">Share :</span>
                  <span>
                    <IconButton className="text-muted px-2">
                      <FacebookIcon />
                    </IconButton>
                    <IconButton className="text-muted px-2">
                      <TwitterIcon />
                    </IconButton>
                    <IconButton className="text-muted px-2">
                      <InstagramIcon />
                    </IconButton>
                    <IconButton className="text-muted px-2">
                      <LinkedInIcon />
                    </IconButton>
                  </span>
                </p>
                <hr className=" mb-4" />

                <p className="text-muted">{productData.description}</p>
                <div>
                  <Button
                    variant="contained"
                    className="flex-fill w-50 btn-cart"
                    onClick={addedcart}
                    sx={{ padding: ".8rem 2rem", background: "#000" }}
                  >
                    Add to Cart
                  </Button>
                  <IconButton className="favorite-icon">
                    <FavoriteBorderOutlinedIcon />
                  </IconButton>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="mt-7">
        <div className="container">
          <h3 className="desc-border mb-2">Description</h3>
          <div className="horizontal-line-desc mb-4"></div>
          <p className="">
            {productData.description}
            {productData.description}
          </p>
        </div>
      </section>
      <section className="mt-7">
        <div className="container">
          <h2>Related Products</h2>
          <div className="row">
            {realtedCatData.map((items, index) => {
              return (
                <div className="col-sm-3 mb-3 product" key={index}>
                  <div className="card">
                    <div className="p-5 ">
                      <img
                        src={items.image}
                        class=" card-img card-img-top "
                        alt="..."
                      />
                    </div>
                    <div class="card-body py-1">
                      <h5 class="card-title m-0">{items.title.slice(0, 20)}</h5>
                      <Typography
                        variant="body2"
                        className="text-muted"
                        gutterBottom
                      >
                        {items.category}
                      </Typography>
                      <Rating
                        initialValue={items.rating.rate}
                        readonly
                        size={24}
                        className="mb-2"
                      />
                      <div className="d-flex justify-content-between align-items-center py-2">
                        <h4>${items.price}</h4>
                        <p className="clr-green">In Stock</p>
                      </div>
                    </div>
                    <div className="position-relative showparent">
                      <div className="position-absolute showdetail px-2 pt-2">
                        <div className="d-flex justify-content-between gap-2 ">
                          <IconButton className="p-2">
                            <FavoriteBorderOutlinedIcon />
                          </IconButton>
                          <Button
                            onClick={() => {
                              singleprop(items.id);
                            }}
                            variant="contained"
                            className="flex-fill rounded-0 btn-card"
                            sx={{ background: "#333" }}
                          >
                            Show Details
                          </Button>
                          <IconButton className="p-2">
                            <VisibilityIcon />
                          </IconButton>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    </>
  );
};

export default Singleproduct;
