import { createSlice } from "@reduxjs/toolkit";
import { ApiData } from "../components/Data";
import { useContext } from "react";
// const data = useContext(ApiData.maindata)
const initialState = {
  cart: [],
  items: [],
  favitems: [],
  totalfavitems: 0,
  totalQuantity: 0,
  totalPrice: 0,
};
export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    getfavitemstotal: (state) => {
      state.totalfavitems = state.favitems.length;
    },
    getfavitems: (state, action) => {
      let indexid = "";

      let favfind = state.favitems.filter(
        (items, index) => items.id === action.payload.id
      );
      console.log(favfind.id);
      if (favfind.length > 0) {
        state.favitems = state.favitems.splice(favfind[0].id, 1);
      } else {
        state.favitems = [...state.favitems, action.payload];
      }
    },
    getdata: (state, action) => {
      state.items = action.payload;
    },
    addToCart: (state, action) => {
      let find = state.cart.findIndex(
        (items) => items.id === action.payload.id
      );
      console.log(find);
      if (find >= 0) {
        state.cart[find].quantity = 1;
        // console.log(true);
      } else {
        // let gun = action.payload.quantity = 1
        state.cart = [...state.cart, { ...action.payload, quantity: 1 }];
      }
    },
    getCartTotal: (state) => {
      let { totalQuantity, totalPrice } = state.cart.reduce(
        (cartTotal, cartItem) => {
          console.log(cartTotal);
          console.log(cartItem);
          const { price, quantity } = cartItem;
          console.log(price, quantity);
          let total = price * quantity;
          console.log(total);
          cartTotal.totalPrice += total;
          cartTotal.totalQuantity += quantity;
          console.log(cartTotal);
          return cartTotal;
        },
        {
          totalQuantity: 0,
          totalPrice: 0,
        }
      );
      state.totalPrice = parseInt(totalPrice.toFixed(2));
      state.totalQuantity = totalQuantity;
    },
    removeItem: (state, action) => {
      state.cart = state.cart.filter(
        (items, index) => items.id !== action.payload
      );
    },
    increaseQuantity: (state, action) => {
      state.cart = state.cart.map((items) => {
        if (items.id === action.payload) {
          // console.log({...items, quantity: items.quantity ++})
          return { ...items, quantity: items.quantity + 1 };
        } else {
          return items;
        }
      });
    },
    decreaseQuantity: (state, action) => {
      state.cart = state.cart.map((items) => {
        if (items.id === action.payload) {
          // console.log({...items, quantity: items.quantity ++})
          return { ...items, quantity: items.quantity - 1 };
        } else {
          return items;
        }
      });
    },
  },
});
console.log(cartSlice);
export const {
  getfavitems,
  getdata,
  addToCart,
  getCartTotal,
  getfavitemstotal,
  removeItem,
  increaseQuantity,
  decreaseQuantity,
} = cartSlice.actions;
export default cartSlice.reducer;
