import React, { useContext, useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import { Typography } from "@mui/material";
import axios from "axios";
import "./navbar.css";
import SortIcon from "@mui/icons-material/Sort";
import { Link, useNavigate } from "react-router-dom";
import TextField from "@mui/material/TextField";
import IconButton from "@mui/material/IconButton";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import SearchIcon from "@mui/icons-material/Search";
import { ApiData } from "./Data";
import Button from "@mui/material/Button";
import ShuffleIcon from "@mui/icons-material/Shuffle";
import VisibilityIcon from "@mui/icons-material/Visibility";
import { Rating } from "react-simple-star-rating";
import { useDispatch, useSelector } from "react-redux";
import { getfavitems } from "../feature/cartSlice";

const Home = () => {
  // all state
  const [mydata, setMyData] = useState([]);
  const [catdata, setCatdata] = useState([]);
  const [searchdata, setSearchData] = useState([]);
  const [smaindata, Setsmaindata] = useState([]);
  const [displaysec, setDisplaySec] = useState(true);
  const [toprated, setToprated] = useState([]);

  // getting data from context
  const getdata = useContext(ApiData);

  // expression of navigate and dispatch
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // getting data from redux store
  const { favitems, cart } = useSelector((state) => state.allcart);
  const favoriteitems = (favproduct) => {
    dispatch(getfavitems(favproduct));
    const favdata = favitems.filter((items, index) => {
      return favproduct.id === items.id;
    });
    if (favdata.length > 0) {
      toast("Product Removed From  Favorite");
    } else {
      toast("Product Added To Favorite");
    }
  };

  //  useEffect
  useEffect(() => {
    setCatdata(getdata.catdata);
    setMyData(getdata.maindata);
    const topproduct = mydata.filter((items, index) => {
      if (items.rating.rate > 4.6) {
        return items;
      }
    });
    setToprated(topproduct);
  });

  //  navigate to category page
  const categorypage = (catName) => {
    navigate(`/singlecat/${catName}`);
  };

  //  navigate to Single Product page
  const singleprop = (dataId) => {
    navigate(`/singleproduct/${dataId}`);
  };

  //  Inputhandler foor search input
  const inputhandler = (event) => {
    setSearchData(event.target.value);
    if (searchdata != null) {
      let newarr = mydata.filter((items, index) => {
        return items.title
          .toLowerCase()
          .includes(event.target.value.toLowerCase());
      });
      Setsmaindata(newarr);
      setDisplaySec(false);
    } else {
      Setsmaindata(mydata);
    }
  };
  return (
    <>
      <ToastContainer />
      <div className="container">
        <div className="row">
          <div className="col-sm-3">
            <div className="home_category ">
              <div className="bg-blck br-round d-flex justify-content-start align-items-center ">
                <SortIcon className="clr-secondary ms-3" fontSize="large" />
                <h5 className="cat-heading clr-secondary py-3 px-3">
                  Categories
                </h5>
              </div>
              <ul>
                {catdata.map((items, index) => {
                  return (
                    <li>
                      <a
                        className="clr-primary"
                        onClick={() => {
                          categorypage(items);
                        }}
                      >
                        {items}
                      </a>
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
          <div className="col-sm-9">
            <div className="inputgrp ">
              <input
                type="text"
                placeholder="Search Products Here"
                className="form-control inputtest"
                onChange={inputhandler}
              />
              <div className="input-cat d-inline-flex">
                <div class="dropdown dropcat">
                  <a
                    class="nav-link dropdown-toggle"
                    href="#"
                    id="navbarDropdown"
                    role="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    All Categories
                  </a>
                  <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                    <li>
                      <a class="dropdown-item" href="#">
                        Men's Fashion
                      </a>
                    </li>
                    <li>
                      <a class="dropdown-item" href="#">
                        Men's Fashion
                      </a>
                    </li>
                    <li>
                      <hr class="dropdown-divider" />
                    </li>
                    <li>
                      <a class="dropdown-item" href="#">
                        Men's Fashion
                      </a>
                    </li>
                  </ul>
                </div>

                <div className="search-btn bg-blck">
                  <IconButton
                    variant="contained"
                    sx={{ bgcolor: "none", borderRadius: "0" }}
                  >
                    <SearchIcon className="clr-secondary font-size-icon" />
                  </IconButton>
                </div>
              </div>
            </div>
            <div
              id="main"
              className="mt-4"
              style={{
                display:
                  displaysec || searchdata.length == 0 ? "block" : "none",
              }}
            >
              <div
                id="carouselExampleCaptions"
                class="carousel slide"
                data-bs-ride="carousel"
              >
                <div class="carousel-inner">
                  <div class="carousel-item active">
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="slider-content bg-tertiary">
                          <Typography
                            variant="h3"
                            className="fw-bolder mb-4"
                            gutterBottom
                          >
                            Spring
                            <span className="bg-blck fw-bolder clr-secondary px-3 pt-3 pb-2 font-josefins">
                              Sale
                            </span>
                          </Typography>
                          <Typography
                            variant="body1"
                            className="fw-light"
                            gutterBottom
                          >
                            Lorem ipsum dolor sit amet consectetur adipisici
                            Lorem ipsum dolor sit amet.
                          </Typography>
                          <button className="btn slider-btn">
                            Explore Now
                          </button>
                          <div className="slider-indicatoreach mt-5">
                            <div class="carousel-indicators">
                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="0"
                                class="active"
                                aria-current="true"
                                aria-label="Slide 1"
                              >
                                01
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="1"
                                aria-label="Slide 2"
                              >
                                02
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="2"
                                aria-label="Slide 3"
                              >
                                03
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col-sm-6 overflow-hidden">
                        <img
                          src="images/slider2.png"
                          class="d-block w-100"
                          alt="..."
                        />
                      </div>
                    </div>
                  </div>
                  <div class="carousel-item">
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="slider-content bg-tertiary">
                          <Typography
                            variant="h3"
                            className="fw-bolder mb-4"
                            gutterBottom
                          >
                            Spring
                            <span className="bg-blck fw-bolder clr-secondary px-3 pt-3 pb-2 font-josefins">
                              Sale
                            </span>
                          </Typography>
                          <Typography
                            variant="body1"
                            className="fw-light"
                            gutterBottom
                          >
                            Lorem ipsum dolor sit amet consectetur adipisici
                            Lorem ipsum dolor sit amet.
                          </Typography>
                          <button className="btn slider-btn">
                            Explore Now
                          </button>
                          <div className="slider-indicatoreach mt-5">
                            <div class="carousel-indicators">
                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="0"
                                aria-current="true"
                                aria-label="Slide 1"
                              >
                                01
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="1"
                                class="active"
                                aria-label="Slide 2"
                              >
                                02
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="2"
                                aria-label="Slide 3"
                              >
                                03
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col-sm-6 overflow-hidden">
                        <img
                          src="images/slider1.jpg"
                          class="d-block w-100"
                          alt="..."
                        />
                      </div>
                    </div>
                  </div>
                  <div class="carousel-item">
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="slider-content bg-tertiary">
                          <Typography
                            variant="h3"
                            className="fw-bolder mb-4"
                            gutterBottom
                          >
                            Spring
                            <span className="bg-blck fw-bolder clr-secondary px-3 pt-3 pb-2 font-josefins">
                              Sale
                            </span>
                          </Typography>
                          <Typography
                            variant="body1"
                            className="fw-light"
                            gutterBottom
                          >
                            Lorem ipsum dolor sit amet consectetur adipisici
                            Lorem ipsum dolor sit amet.
                          </Typography>
                          <button className="btn slider-btn">
                            Explore Now
                          </button>
                          <div className="slider-indicatoreach mt-5">
                            <div class="carousel-indicators">
                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="0"
                                aria-current="true"
                                aria-label="Slide 1"
                              >
                                01
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="1"
                                aria-label="Slide 2"
                              >
                                02
                              </button>

                              <button
                                type="button"
                                data-bs-target="#carouselExampleCaptions"
                                data-bs-slide-to="2"
                                class="active"
                                aria-label="Slide 3"
                              >
                                03
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col-sm-6 overflow-hidden">
                        <img
                          src="images/slider3.png"
                          class="d-block w-100"
                          alt="..."
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* fashion section */}

      <section
        id="men-women-fashion"
        className="mt-5"
        style={{
          display: displaysec || searchdata.length == 0 ? "block" : "none",
        }}
      >
        <div className="container">
          <div className="row">
            <div className="col-sm-6 mens-fashion position-relative">
              <div className="overflow-hidden">
                <img src="images/cat-2.png" className="img-fluid" alt="" />
                <div className="mens-fashion-content position-absolute pt-6 pb-3 px-5">
                  <Typography variant="h4" className="fw-bold">
                    Men's
                  </Typography>
                  <button className="btn slider-left-btn  ">
                    Trending 2023
                  </button>
                </div>
              </div>
            </div>
            <div className="col-sm-6 mens-fashion position-relative ">
              <div className="overflow-hidden ">
                <img
                  src="images/cat-1.jpg"
                  className="img-fluid h-100  "
                  alt=""
                />
              </div>
              <div className="mens-fashion-content position-absolute pt-6 pb-3 px-5">
                <Typography variant="h4" className="fw-bold">
                  Women's
                </Typography>
                <button className="btn slider-left-btn  ">Trending 2023</button>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* to show data from api section */}

      <section id="apidata" className="mt-5">
        <div className="container">
          {/* <div className="row"> */}
          {/* <div className="col-sm-9"> */}
          {/* <div className={searchdata.length == 0 ? "col-sm-12" : "col-sm-9"}> */}
          <h3 className="text-center pt-5 pb-3">Our Trendy Products</h3>
          <div className="filterproducts text-center pb-5">
            <a href="" className="py-2 px-3 clr-primary">
              All
            </a>
            <a href="" className="py-2 px-3 text-muted">
              Popular
            </a>
            <a href="" className="py-2 px-3 text-muted">
              Sale
            </a>
            <a href="" className="py-2 px-3 text-muted">
              Best Rated
            </a>
          </div>
          <div className="row">
            {searchdata.length > 0
              ? smaindata.map((items, index) => {
                  return (
                    <div className="col-sm-12 col-lg-3 col-md-3 mb-3 product" key={index}>
                      <div className="card">
                        <div className="p-5 ">
                          <img
                            src={items.image}
                            class=" card-img card-img-top "
                            alt="..."
                          />
                        </div>
                        <div class="card-body py-1">
                          <h5 class="card-title m-0">
                            {items.title.slice(0, 20)}
                          </h5>
                          <Typography
                            variant="body2"
                            className="text-muted"
                            gutterBottom
                          >
                            {items.category}
                          </Typography>
                          <Rating
                            initialValue={items.rating.rate}
                            readonly
                            size={24}
                            className="mb-2"
                          />
                          <div className="d-flex justify-content-between align-items-center py-2">
                            <h4>${items.price}</h4>
                            <p className="clr-green">In Stock</p>
                          </div>
                        </div>
                        <div className="position-relative showparent">
                          <div className="position-absolute showdetail px-2 pt-2">
                            <div className="d-flex justify-content-between gap-2 ">
                              <IconButton
                                className="p-2"
                                onClick={() => favoriteitems(items)}
                              >
                                <FavoriteBorderOutlinedIcon />
                              </IconButton>
                              <Button
                                onClick={() => {
                                  singleprop(items.id);
                                }}
                                variant="contained"
                                className="flex-fill rounded-0 btn-card"
                                sx={{ background: "#333" }}
                              >
                                Show Details
                              </Button>
                              <IconButton className="p-2">
                                <VisibilityIcon />
                              </IconButton>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })
              : mydata.map((items, index) => {
                  return (
                    <div className="col-sm-12 col-lg-3 col-md-3 mb-3 product" key={index}>
                      <div className="card">
                        <div className="p-5 ">
                          <img
                            src={items.image}
                            class=" card-img card-img-top "
                            alt="..."
                          />
                        </div>
                        <div class="card-body py-1">
                          <h5 class="card-title m-0">
                            {items.title.slice(0, 20)}
                          </h5>
                          <Typography
                            variant="body2"
                            className="text-muted"
                            gutterBottom
                          >
                            {items.category}
                          </Typography>
                          <Rating
                            initialValue={items.rating.rate}
                            readonly
                            size={24}
                            className="mb-2"
                          />
                          <div className="d-flex justify-content-between align-items-center py-2">
                            <h4>${items.price}</h4>
                            <p className="clr-green">In Stock</p>
                          </div>
                        </div>
                        <div className="position-relative showparent">
                          <div className="position-absolute showdetail px-2 pt-2">
                            <div className="d-flex justify-content-between gap-2 ">
                              <IconButton
                                className="p-2"
                                onClick={() => favoriteitems(items)}
                              >
                                <FavoriteBorderOutlinedIcon />
                              </IconButton>
                              <Button
                                onClick={() => {
                                  singleprop(items.id);
                                }}
                                variant="contained"
                                className="flex-fill rounded-0 btn-card"
                                sx={{ background: "#333" }}
                              >
                                Show Details
                              </Button>
                              <IconButton className="p-2">
                                <VisibilityIcon />
                              </IconButton>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
          </div>
          <div className="text-center">
            <button className="btn slider-btn fw-semibold ">
              View All Products
            </button>
          </div>
          {/* </div> */}
          {/* </div> */}
          {/* </div> */}
        </div>
        
      </section>

      {/* discount section */}
      <section id="discountsection" className="mt-5">
        <div className="container pt-7 pb-7">
          <h1 className="font-size-4">
            End of Season Clearance <br />
            <strong>Sale upto 30%</strong>
          </h1>
          <p className="text-muted">
            Stock is limited. Order now to avoid <br />
            disappointment.
          </p>
          <Button
            variant="contained"
            className="mt-2 mb-4 btn-bor"
            sx={{ background: "#fff", color: "#333", padding: ".8rem 2.2rem" }}
            size="large"
          >
            Shop Now
          </Button>
        </div>
      </section>

      {/* Most Searched product section */}

      <section className="mt-5">
        <div className="container">
          <h3 className="text-center pt-5 pb-5">Our Top Rated Products</h3>
          <div className="row g-5">
            {toprated.map((items, index) => {
              return (
                <>
                  <div className="col-sm-1"></div>
                  <div className="col-sm-5 position-relative">
                    <div className="card toprated-card">
                      <div className="p-5 text-center">
                        <img
                          src={items.image}
                          class="ratedimg img-fluid sizing"
                          // style={{ width: "300px", height: " 300px" }}
                          alt="..."
                        />
                      </div>
                    </div>
                    <div className="card-body card-top-reated-pos">
                      <h3>{items.title.slice(0, 13)}</h3>
                      <p className="text-muted">
                        {items.description.slice(0, 20)}
                        <br />
                        {items.description.slice(20, 40)}
                      </p>
                      <div>
                        <Rating
                          initialValue={items.rating.rate}
                          readonly
                          size={20}
                        />
                      </div>
                      <button
                        className="btn slider-left-btn1"
                        onClick={() => {
                          singleprop(items.id);
                        }}
                      >
                        Shop Now
                      </button>
                    </div>
                  </div>
                </>
              );
            })}
          </div>
        </div>
      </section>
    </>
  );
};
export default Home;
